//  : components/BoardDetail/subcomponents/CommentBox.js

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { CommentInput, CommentUD } from './';
import { 
    PhotoTextItem,
    CommentItemPhoto,
    CommentItemText,
    TextZone, 
} from './CommentItem'
import { colors } from "../../../../styles/variables";


/* Styled Components */

/*    (Photo + Text + )
      ,  (  ),      */
const SubCommentLeaf = styled.div`
    margin-left: 3rem;
    margin-bottom: 0.5rem;

    display: ${props => {
        return props.isDisplay ? 'block' : 'none'
    }};
    /* flex-flow: row nowrap; */
`;

    /* Sub Leaf   Div  */
    const CreatedDivForSub = styled.div`
        position: relative;
        height: 100%;
        line-height: 1.33em;
        font-size: 0.75rem;
        color: ${colors.gray_fontColor};
        white-space: nowrap;            /*    !! */

        flex: 1 auto;
    `;

    /* position: absolute    ,   div  */
    const HiddenDiv = styled.div`
        visibility: hidden;             /*      !! */
    `;

    /* created         div  */
    const DivForPosition = styled.div`
        position: absolute;
        bottom: 0.5em;
    `;

    /*      (Input  +  ) */
    const UpdateComponent = styled.div`
        display: flex;
        flex-flow: column nowrap;
    `;

    /*    div  */
    const CancleTab = styled.div`
        margin-top: 0.25rem;
        margin-left: 3rem;

        font-size: 0.75rem;
        color: ${colors.font_gray};
    `;

    /* ''    */
    const CancleText = styled.span`
        color: ${colors.blue};
        
        cursor: pointer;
        :hover {
            text-decoration: underline;
        }
    `;

//////////////////////////////////////

/* React Component */

class SubCommentItem extends Component {

    state = {
        isUDVisible: false,         //    visible
        isUpdateMode: false,        //   
    }

    /*     ,
            update, delete    */
    _handleMouseEnter = () => {
        /* user , curUser   */
        const {
            user, curUser
        } = this.props;

        if (user.username === curUser.username) {
            this.setState({
                ...this.state,
                isUDVisible: true
            })
        }
    }

    /*     visible: false */
    _handleMouseLeave = () => {
        this.setState({
            ...this.state,
            isUDVisible: false
        })
    }
    ///////////////////

    /*     */
    _setUpdateMode = () => {
        this.setState({
            ...this.state,
            isUpdateMode: true
        })
    }

    /*     */
    _handleUpdateCancle = () => {
        this.setState({
            ...this.state,
            isUpdateMode: false
        })
    }


    render() {

        const {
            curUser,            //   
            updateComment,      //    (Comment Input  )

            subComment_id,
            deleteComment,

            isReplySee,

            user,
            description,
            created,
        } = this.props;

        const { 
            isUDVisible,
            isUpdateMode,
        } = this.state;

        return (
            <SubCommentLeaf 
                isDisplay={isReplySee} 
                onMouseEnter={this._handleMouseEnter}
                onMouseLeave={this._handleMouseLeave}
            >
                {
                    isUpdateMode ?

                    <UpdateComponent>
                        <CommentInput 
                            curUser={curUser}
                            isUpdateMode={true} 
                            updateComment={updateComment}
                            comment_id={subComment_id}
                            defaultValue={description}
                            cancleUpdate={this._handleUpdateCancle}
                        />
                        <CancleTab>
                            <CancleText onClick={this._handleUpdateCancle} ></CancleText>
                            <span> Esc  .</span>
                        </CancleTab>
                    </UpdateComponent> :
                    
                    <PhotoTextItem>
                        <CommentItemPhoto />
                        <TextZone>
                            <CommentItemText>
                                <b>{user.username}</b>&nbsp;
                                {description.split('\n').map(line => {
                                    return (<span>{line}<br/></span>)
                                })}
                            </CommentItemText>
                            {
                                isUDVisible ?
                                <CommentUD 
                                    comment_id={subComment_id}
                                    deleteComment={deleteComment}

                                    setUpdateMode={this._setUpdateMode}
                                /> :
                                <CreatedDivForSub>
                                    <HiddenDiv>{created}</HiddenDiv> 
                                    <DivForPosition>{created}</DivForPosition>
                                </CreatedDivForSub>
                            }
                        </TextZone>
                    </PhotoTextItem>
                }
                
            </SubCommentLeaf>
        )
    }
}


/* propTypes, defaultProps */
SubCommentItem.propTypes = {
    curUser: PropTypes.oneOfType([                  //  
        PropTypes.number,
        PropTypes.object
    ]).isRequired,
    subComment_id: PropTypes.number.isRequired,     //  comment id
    deleteComment: PropTypes.func.isRequired,       // UD   delete action

    isReplySee: PropTypes.bool.isRequired,          //   true / false

    user: PropTypes.object.isRequired,              //   
    description: PropTypes.string.isRequired,       //  
    created: PropTypes.string.isRequired,           //   
}

SubCommentItem.defaultProps = {
}


export default SubCommentItem;