

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import PropTypes from 'prop-types';
import SocketIo from 'socket.io-client';

import { getStatusRequest, logoutRequest } from "../store/actions/auth";
import ChattingHeader from "../components/Chatting/ChattingHeader";
import ChattingBody from "../components/Chatting/ChattingBody";
import { 
    getBeginingRoomListRequest,
    getBeginingChatDataRequest,
} from '../store/actions/chatting';


class ChattingContainer extends Component {

    chatSocket = SocketIo.connect('/chatting');

    componentDidMount() {
        const { 
            isIndex, match,
            
            getStatusRequest,
            getBeginingRoomListRequest,
            getBeginingChatDataRequest,
        } = this.props;
        
        const opntID = match ? match.params.userID : 0;     // opponent ID.   ID (index   0)

        getStatusRequest();

        getBeginingRoomListRequest();

        if ( !isIndex ) { getBeginingChatDataRequest(opntID) }
    }

    render() {

        const { 
            isIndex,
            match,
            curUser, 
            isGetStatusDone,
            roomList,
        
            logoutRequest
        } = this.props;

        const opntID = match ? match.params.userID : 0;     // opponent ID.   ID (index   0)

        console.log(roomList);

        return(
            isGetStatusDone ?

                curUser ?

                <div style={{height: '100%', width: '100%'}}>
                    <ChattingHeader 
                        curUser={curUser}
                        logoutRequest={logoutRequest}
                    />
                    <ChattingBody 
                        isIndex={isIndex} 
                        opntID={opntID} 
                        chatSocket={this.chatSocket}    
                    />
                </div> :

                <Redirect to='/' /> :

            ''
        )
    }
}

ChattingContainer.propTypes = {
    isIndex: PropTypes.bool,        // index   
    match: PropTypes.object,        //  user ID   url params  
}

ChattingContainer.defaultProps = {
    isIndex: false,
}


const mapStateToProps = (state) => {
    return {
        curUser: state.auth.user,                               //   
        isGetStatusDone: state.auth.isGetStatusDone,            // get status   
        roomList: state.chatting.roomList,                      //   
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getStatusRequest: () => dispatch(getStatusRequest()),           //     request 
        logoutRequest: () => dispatch(logoutRequest()),                 //  GET request 
        
        getBeginingRoomListRequest: () => {                             // Chatting    RoomList Data  
            dispatch(getBeginingRoomListRequest())
        },
        getBeginingChatDataRequest: (opntID) => {                       //  Chatting      
            dispatch(getBeginingChatDataRequest(opntID))
        },
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(ChattingContainer);