//  : components/BoardDetail.js

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { CommentItem, CommentInput } from './Comment_Materials'
import { colors } from "../../../styles/variables";

/* Styled Components */

/* CommentBox  div */
const CommentArea = styled.div`
    padding: 1.5rem 1rem;
    background-color: ${colors.gray_bg};
    border-radius: 8px;
    width: 100%;
    min-height: 10rem;
    position: relative;

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
`;

/*    +      */
const CommentHeader = styled.div`
    width: 100%;
    margin-bottom: 1.5rem;

    height: 2rem;
    line-height: 2rem;
    font-size: 1.5rem;
`;

/* CommentInput  margin      */
const BottomOfInputDiv = styled.div`
    width: 100%;
    padding-bottom: 1.5rem;
    margin-bottom: 1.5rem;
    border-bottom: 1px solid ${colors.white};
`;

/////////////////////////////////////////

/* React Components */

class CommentBox extends Component {

    //   (isMoreSee) 

    componentDidMount() {
    }

    // map  array  render 
    _renderComments = (commentList) => {

        const { 
            curUser,

            updateComment,
            deleteComment,

            /* CommentInput   */
            board,
            post_id,
            createComment, 
        } = this.props;

        return commentList.map((comment) => {
            const {
                id,
                user,
                description,
                created,
                subCommentList
            } = comment;

            // console.log(comment)

            return (
                <CommentItem
                    curUser={curUser}

                    comment_id={id}
                    user={user}
                    description={description}
                    created={created}
                    subCommentList={subCommentList}
                    updateComment={updateComment}
                    deleteComment={deleteComment}
                    key={id}

                    /* CommentInput   */
                    board={board}
                    post_id={post_id}
                    createComment={createComment}
                />
            )
        })
    }

    render() {

        const { 
            curUser,

            board,
            post_id,
            commentList,

            createComment, 
        } = this.props;
        const numOfComments = commentList.length;

        // console.log(commentList);

        return (
            <CommentArea>
                
                {/*   +    */}
                <CommentHeader>
                    {numOfComments}  
                </CommentHeader>

                {/*    */}
                <CommentInput 
                    isSubComment={false} 
                    
                    curUser={curUser}

                    board={board}
                    post_id={post_id}
                    createComment={createComment}
                />

                {/*        */}
                <BottomOfInputDiv />
                
                {/*  render */}
                {
                    numOfComments ?
                    this._renderComments(commentList) :
                    '  .'
                }

                {/* //////////////////////////// */}
            </CommentArea>
        )
    }
}


CommentBox.propTypes = {
    curUser: PropTypes.oneOfType([                  //  
        PropTypes.number,
        PropTypes.object
    ]).isRequired,

    board: PropTypes.string.isRequired,             //  
    post_id: PropTypes.number.isRequired,           //  id          
    commentList: PropTypes.array.isRequired,        //     array

    createComment: PropTypes.func.isRequired,       //   
    updateComment: PropTypes.func.isRequired,       //   
    deleteComment: PropTypes.func.isRequired,       //   
}

// CommentBox.defaultProps = {
//     commentList: []
// }

export default CommentBox;