//  : BoardDetailContainer

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { 
    HeaderBox,
    PhotoBox,
    DescriptionBox,
    CommentBox,
} from "./subcomponents";

import { colors } from "../../styles/variables";


/* Styled Components */

/*     div  */
const BackgroundDiv = styled.div`
    width: 100%;
    padding-left: 15rem;
    background-color: ${colors.gray_bg};
`;

/*     div 
         , content         .
       70%      */
const ContainerDiv = styled.div`
    width: 70%;
    min-width: 50rem;
    min-height: ${props => props.minHeight};
    margin: 0 auto;
    padding: 3rem 2rem;

    background-color: ${colors.white};

    /*   */
    box-shadow: 0 0 10px rgba(0,0,0,.1);
`;


/* React Component */

class BoardDetail extends Component {

    state = {}

    componentDidMount() {
        this.setState({
            clientHeight: document.documentElement.clientHeight     //    
        })

        /*      . */
        window.addEventListener('resize', this._handleResize) 
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._handleResize)
    }

    _handleResize = (e) => {
        this.setState({
            ...this.state,
            clientHeight: document.documentElement.clientHeight
        })
    }

    render() {
        
        let { clientHeight } = this.state;

        const {
            /* PhotoBox   */
            filelist,           //     (array)

            /* TextBox   */
            title,              //  (string)
            user,               //    (object)
            created,            //  (string)
            condition,          //  
            description,        //  

            price,              // Market Post    

            /* CommentBox   */
            id,                 //  
        } = this.props.post;

        // console.log(this.props.post);
        // console.log(filelist);

        let isPhoto = filelist[0];     // filelist    true

        const {
            curUser,            //   

            board,              //   
            commentList,        // comment data

            createComment,      //   
            updateComment,      //   
            deleteComment,      //   
        } = this.props;

        // console.log(commentList);
        // console.log(curUser)

        return (
            <BackgroundDiv>
                <ContainerDiv minHeight={`${clientHeight}px`} >
                    <HeaderBox
                        title={title}
                        user={user}
                        created={created}
                        condition={condition}
                        price={price}
                    />
                    { isPhoto ? <PhotoBox filelist={filelist} /> : '' }
                    <DescriptionBox description={description} />
                    <CommentBox 
                        curUser={curUser}
                        board={board}
                        post_id={id}
                        commentList={commentList} 
                        createComment={createComment}
                        updateComment={updateComment}
                        deleteComment={deleteComment}
                    />
                </ContainerDiv>
            </BackgroundDiv>
        )
    }
}

BoardDetail.propTypes = {
    curUser: PropTypes.oneOfType([                 //  
        PropTypes.number,
        PropTypes.object
    ]).isRequired,

    board: PropTypes.string.isRequired,         //  
    post: PropTypes.object.isRequired,          //  
    commentList: PropTypes.array,               //  

    createComment: PropTypes.func.isRequired,   //   
    updateComment: PropTypes.func.isRequired,   //   
    deleteComment: PropTypes.func.isRequired,   //   
}

BoardDetail.defaultProps = {
    commentList: [],
}

export default BoardDetail;
