//  : pages/Board

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import PropTypes from 'prop-types';

import SideBar from "../components/Structure/SideBar";
import BoardDetail from "../components/BoardDetail";
import ExceptionalPage from '../components/Exceptionals';

import { getStatusRequest } from '../store/actions/auth'
import { 
    initiateDetailPage,
    getBoardDetailRequest,
    createComment,
    updateComment,
    deleteComment,
} from '../store/actions/board';
import { topicSelect } from "../store/actions/topic";

class BoardDetailContainer extends Component {

    componentDidMount () {
        const {
            match,

            topicSelect,
            initiateDetailPage,
            getStatusRequest,
            getBoardDetailRequest,
        } = this.props
        
        const { 
            boardName, id 
        } = match.params;

        initiateDetailPage();
        topicSelect(boardName);

        getStatusRequest();                      //   request
        getBoardDetailRequest(boardName, id);    //   request
    }

    render() {
        const { 
            curUser,
            isGetStatusDone,

            isDetailGetSuccess, 
            post,
            commentList,
            
            createComment,
            updateComment,
            deleteComment,
        } = this.props;

        /*   */
        const { boardName } = this.props.match.params;

        const isLoaded = isGetStatusDone && isDetailGetSuccess;

        return isLoaded ?
            curUser ?
                <div>
                    <SideBar topic={boardName} />

                    <BoardDetail 
                        curUser={curUser}

                        board={boardName}
                        post={post} 
                        commentList={commentList} 

                        createComment={createComment}
                        updateComment={updateComment}
                        deleteComment={deleteComment}
                    />
                </div> :
                <Redirect to='/' /> :
            <ExceptionalPage /> //   + id   
    }
}

BoardDetailContainer.propTypes = {
    match: PropTypes.object.isRequired,         // url    . params.id  id .
}


const mapStateToProps = (state) => {
    return {
        curUser: state.auth.user,                                  //  
        isGetStatusDone: state.auth.isGetStatusDone,            // get status   

        isDetailGetSuccess: state.board.isDetailGetSuccess,     // GET    
        post: state.board.post,                                 //  
        commentList: state.board.commentList,                   //   
    }
}


const mapDispatchToProps = (dispatch) => {
    return {
        topicSelect: (boardName) => {                                                   //  
            dispatch(topicSelect(boardName))
        },    
        
        getStatusRequest: () => {dispatch(getStatusRequest())},                         //    request 

        initiateDetailPage: () => {dispatch(initiateDetailPage())},                     //       
        getBoardDetailRequest: (boardName, id) => {                                     // backend  GET 
            dispatch(getBoardDetailRequest(boardName, id))
        },
        createComment: (description, type_board, board_id, parent_comment) => {         //   
            dispatch(createComment(description, type_board, board_id, parent_comment))
        },
        updateComment: (comment_id, description) => {                                   //   
            dispatch(updateComment(comment_id, description))
        },
        deleteComment: (comment_id) => {dispatch(deleteComment(comment_id))},           //   
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(BoardDetailContainer);