//  : components/BoardDetail/subcomponents/CommentBox.js

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { 
    SubCommentItem,
    CommentInput,
    CommentUD
} from './';
import { colors } from "../../../../styles/variables";


/* Styled Components */

/* Root Leaf + Sub Leaf 
        div */
const CommentStem = styled.div`
    margin-bottom: ${props => {
        return props.isReplySee ? '2rem' : '1rem'
    }};
`;

/*    (PhotoTextItem + AdditionalFucDiv)
    ,  (  ),   ,   . */
const CommentLeaf = styled.div`
   margin: 0;
   margin-bottom: 0.5rem;
`;

/*  ,    div */
export const PhotoTextItem = styled.div`
   width: 100%;
   margin: 0;
   /* margin-bottom: 0.5rem; */

   display: flex;
   flex-direction: row;
   flex-wrap: nowrap;
`;

    /*    */
    export const CommentItemPhoto = styled.div`
        height: 2.5rem;
        width: 2.5rem;
        border-radius: 50%;
        background-color: ${colors.white};

        flex-basis: 2.5rem;
    `;

    /*  2  ,       */
    export const TextZone = styled.div`
        margin-left: 0.5rem;
        flex: 1;

        display: flex;
        flex-flow: row nowrap;
        align-items: center;
    `;

    /*       div  */
    export const CommentItemText = styled.div`
        margin-right: 1rem;
        border-radius: 1rem;
        background-color: ${colors.white};
        padding: 0.5rem 1rem;

        line-height: 1.5rem;
        font-size: 0.875rem;

        display: inline-block;
    `;

/* ,    */
const AdditionalFuncDiv = styled.div`
    margin: 0;
    margin-top: 0.5rem;
    margin-left: 3.75rem;
   
    height: 1rem;
    line-height: 1.33em;
    font-size: 0.75rem;
    color: ${colors.gray_fontColor};

    display: flex;
    flex-flow: row nowrap;
`;

    /*   */
    const TextButton = styled.button`
        margin-left: 2em;

        border: none;
        outline: 0;
        text-decoration: none;
        background-color: ${colors.gray_bg};
        cursor: pointer;
    `;

    const UpdateInfo = styled.div`
    `;


//////////////////////////////////////

/* React Component */

class CommentItem extends Component {

    state = {
        isReplySee: false,              //   True / False
        isUDVisible: false,             //    visible
        isUpdateMode: false,            //   
    }

    /* subCommentList map  render  */
    _renderSubComment = (subCommentList) => {
        const { 
            curUser,
            deleteComment,
            updateComment
        } = this.props;

        // console.log(subCommentList)

        return subCommentList.map((subComment, idx) => {
            const {
                id,
                user,
                description,
                created,
            } = subComment;

            const {isReplySee} = this.state;

            return (
                <SubCommentItem 
                    curUser={curUser}
                    deleteComment={deleteComment}
                    updateComment={updateComment}
                    subComment_id={id}

                    isReplySee={isReplySee} 
                    user={user}
                    description={description}
                    created={created}
                    key={idx}
                />
            )
        })
    }

    /*         */
    _handleReplySee = () => {
        // let changedValue = this.state.isReplySee ? false : true;
        let changedValue = this.state.isReplySee ? false : true;

        this.setState({
            ...this.state,
            isReplySee: changedValue
        })
    }

    /*     ,
            update, delete    */
    _handleMouseEnter = () => {
        /* user , curUser   */
        const {
            user, curUser
        } = this.props;

        if (user.username === curUser.username) {
            this.setState({
                ...this.state,
                isUDVisible: true
            })
        }
    }

    /*     visible: false */
    _handleMouseLeave = () => {
        this.setState({
            ...this.state,
            isUDVisible: false
        })
    }
    ///////////////////

    /*     */
    _setUpdateMode = () => {
        this.setState({
            ...this.state,
            isUpdateMode: true
        })
    }

    /*     */
    _handleUpdateCancle = () => {
        this.setState({
            ...this.state,
            isUpdateMode: false
        })
    }


    render() {

        const {
            curUser,

            comment_id,
            user,
            description,
            created,
            subCommentList,
            deleteComment,

            board,
            post_id,
            createComment,
            updateComment,
        } = this.props;

        const { isReplySee, isUDVisible, isUpdateMode } = this.state;

        let NumOfSubComment = subCommentList.length;

        // console.log(typeof(isUDVisible))

        return (
            /* Comment Stem:  +      */
            <CommentStem isReplySee={isReplySee} > 
                {/* Comment Leaf:   +    */}
                <CommentLeaf>
                    {
                        /* update   Input  render
                               Photo + Text  render */
                        isUpdateMode ?
                        
                        <CommentInput
                            curUser={curUser}
                            isUpdateMode={isUpdateMode} 
                            updateComment={updateComment}
                            comment_id={comment_id}
                            defaultValue={description}
                            cancleUpdate={this._handleUpdateCancle}
                        /> :

                        <PhotoTextItem
                            onMouseEnter={this._handleMouseEnter}
                            onMouseLeave={this._handleMouseLeave}
                        >
                            <CommentItemPhoto />
                            <TextZone>
                                {/*      ( +  ) */}
                                <CommentItemText>
                                    <b>{user.username}</b>&nbsp;
                                    {/* \n   span  + br  
                                           */}
                                    {description.split('\n').map( line => {
                                        return (<span>{line}<br/></span>)
                                    })}
                                </CommentItemText>
                                
                                {/*     */}
                                <CommentUD 
                                    comment_id={comment_id} 
                                    subCommentList={subCommentList} 
                                    deleteComment={deleteComment}
                                    isVisible={isUDVisible}

                                    setUpdateMode={this._setUpdateMode}
                                />
                            </TextZone>
                        </PhotoTextItem>
                    }
                    
                    {/*   +    +   */}
                    <AdditionalFuncDiv>
                        <span>{created}</span>
                        <TextButton onClick={this._handleReplySee} >
                            {
                                /*   : ' '
                                      : 
                                         : 'n  '
                                         : '          */ 

                                isReplySee ?
                                ' ' :
                                NumOfSubComment ?
                                    `${NumOfSubComment}  ` :
                                    ' '
                            }
                        </TextButton>

                        {/*    ,
                                   */}
                        <UpdateInfo>
                            {
                                isUpdateMode ?
                                <TextButton onClick={this._handleUpdateCancle}></TextButton> :
                                ''
                            }
                        </UpdateInfo>
                    </AdditionalFuncDiv>
                </CommentLeaf>

                {/* . map    */}
                {
                    NumOfSubComment ?
                    this._renderSubComment(subCommentList) :
                    ''
                }

                {/*    */}
                <CommentInput
                    isSubComment={true}
                    isReplySee={isReplySee}

                    curUser={curUser}

                    board={board}
                    post_id={post_id}
                    parent_comment={comment_id}
                    createComment={createComment}
                />

            </CommentStem>
        )
    }
}


/* propTypes, defaultProps */

CommentItem.propTypes = {
    curUser: PropTypes.oneOfType([              //  
        PropTypes.number,
        PropTypes.object
    ]).isRequired,

    comment_id: PropTypes.number.isRequired,    //  ID
    user: PropTypes.object.isRequired,          //  
    description: PropTypes.string.isRequired,   //  
    created: PropTypes.string.isRequired,       //  
    subCommentList: PropTypes.array,            //   array
    deleteComment: PropTypes.func.isRequired,   //   
    updateComment: PropTypes.func.isRequired,   //   

    /* CommentInput   */
    board: PropTypes.string.isRequired,         //  
    post_id: PropTypes.number.isRequired,       //  id          
    createComment: PropTypes.func.isRequired,   //   
}

CommentItem.defaultProps = {
    subCommentList: []
}


export default CommentItem;