/* Chatting        
    */

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import BackgroundTemplate from "../Structure/CommonComponents/BackgroundTemplate";
import { ChatRoomBox, IndexChatRoomBox } from "./ChatRoomBox";
import ContectListBox from "./ContectListBox";
import { colors } from "../../styles/variables";


/* Styled Components */
/* Body     
   padding    ,      padding . */
const ContentArea = styled.div`
    padding: 1.5rem;
    width: 100%;
    height: 100%;

    display: flex;
    flex-flow: row nowrap;
    justify-content: center;

    @media (max-width: 935px) {
        padding: 0;
    }
`;

    /*   (  +  )   */
    const ContentBox = styled.div`
        height: 100%;
        width: 100%;
        max-width: 935px;

        background-color: ${colors.white};
        border: ${colors.gray_line} solid 1px;

        display: flex;
        flex-flow: row nowrap;
    `;

        /*      */
        const ContectListBoxArea = styled.div`
            flex: 0 0 350px;
            height: 100%;

            border-right: ${colors.gray_line} solid 1px;
        `;

        /*     */
        const ChatRoomBoxArea = styled.div`
            flex: 1;
            height: 100%;
        `;


/* React Component */
class ChattingBody extends Component {


    _renderContent = () => {

        const { isIndex, opntID } = this.props;

        return(
            <ContentArea>
                <ContentBox>
                    <ContectListBoxArea >
                        <ContectListBox opntID={opntID} />
                    </ContectListBoxArea>
                    
                    <ChatRoomBoxArea>
                    {
                        isIndex ?
                        <IndexChatRoomBox /> :
                        <ChatRoomBox opntID={opntID} />
                    }
                    </ChatRoomBoxArea>
                </ContentBox>
            </ContentArea>
        )
    }

    render() {

        return (
            <BackgroundTemplate 
                doesHaveHeader={true}
                ContentComponent={this._renderContent} 
            />
        )
    }
}

ChattingBody.propTypes = {
    isIndex: PropTypes.bool.isRequired,     //    
    opntID: PropTypes.number.isRequired,    // Opponent ID.   ID
}


export default ChattingBody