import React from 'react';
import styled from 'styled-components';

import { colors } from "../../../styles/variables";

import userPhoto from "./resources/User_Basic_img.JPG";

/* Styled Components */

/* HeaderBox   div  */
const HeaderArea = styled.div`
    position: relative;
    
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid ${colors.gray_bg};
`;


/*  , ,      */
const InfoBox = styled.div`
    height: 4.5rem;
    width: 100%;
    margin-bottom: 1rem;
    
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
`;

/*  User  */
const UserPhoto = styled.div`
    height: 4.5rem;
    width: 4.5rem;
    position: relative;
    border-radius: 50%;         /*     */
    border: ${colors.gray_bg} solid 2px;

    margin-right: 1rem;
    flex: 0, 4.5rem;

    /*    */
    background-image: ${props =>
        props.hasPhoto ? 'none': `url(${userPhoto})`};      /* user photo    */
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
`;

/* ,    div */
const Infomations = styled.div`
    padding-top: 1rem;
    height: 3.5rem;

    display: flex;
    flex-direction: column;

    flex: 1;
`;

/* ,    */
const InfomationText = styled.p`
    margin: 0;
    height: 1.5rem;
    line-height: 1.5rem;
    
    color: ${colors.gray_fontColor};
`;

/*    */
const StatusBox = styled.div`
    height: 4.5rem;
    width: 4rem;
    
    line-height: 4.5rem;
    text-align: center;
    
    flex: 0, 4rem;
`;

/*  */
const Title = styled.h1`
    margin: 0;
    margin-top: 2rem;

    font-size: 2rem;
`

/*  */
const Price = styled.h3`
    margin: 0;
    margin-top: 1.5rem;
    
    font-size: 1.375rem;
`;

///////////////////////////////////

/* React Component */

const HeaderBox = ({title, user, created, condition, price}) => {

    let doesUserHavePhoto = user.profile_picture;

    return (
        <HeaderArea>
            <InfoBox>
                <UserPhoto hasPhoto={doesUserHavePhoto} imgsrc={userPhoto} />   { /* user photo     */ }
                <Infomations>
                    <InfomationText>{user.username}</InfomationText>
                    <InfomationText>{created}</InfomationText>
                </Infomations>
                <StatusBox>{condition}</StatusBox>
            </InfoBox>
            <Title>{title}</Title>
            <Price>{price} </Price> 
        </HeaderArea>
    )
}

export default HeaderBox;