

import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { PaperTemplate } from "../../../../styles/templates/manage";
import { EditTitle, DropZone, Description } from './Subcomponents'

/* Styled Components */

/*     */
const EditPaper = styled(PaperTemplate)`
    min-height: ${props => {
        return `${props.minHeight - 64}px`      // 64 Header 
    }};
    margin: 0 auto;
    width: 55rem;
    min-width: 40rem;
    padding: 3rem 4rem;
    padding-bottom: 5rem;

    display: flex;
    flex-flow: column nowrap;
`;

/* React Component */

const EditBoard = ({ minHeight, title, storeTitleData, 
                     files, addFileData, deleteFileData,
                     description, storeDescriptionData }) => {

    return (
        <EditPaper minHeight={minHeight} >
            <EditTitle title={title} storeTitleData={storeTitleData} />
            <DropZone files={files} addFileData={addFileData} deleteFileData={deleteFileData} />
            <Description description={description} storeDescriptionData={storeDescriptionData} />
        </EditPaper>
    )
}

EditBoard.propTypes = {
    minHeight: PropTypes.number.isRequired,             // Edit Paper   

    title: PropTypes.string.isRequired,                 // Edit   Title 
    files: PropTypes.array.isRequired,                  // Edit    
    description: PropTypes.string.isRequired,           // Eidt   Description 

    storeTitleData: PropTypes.func.isRequired,          // Title  App State  
    addFileData: PropTypes.func.isRequired,             // Files  App State  
    deleteFileData: PropTypes.func.isRequired,          // App State        
    storeDescriptionData: PropTypes.func.isRequired,    // Description  App State  
}

export default EditBoard;