//  : components/BoardDetail/subcomponents/CommentBox.js

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import TextareaAutosize from 'react-textarea-autosize';
import { message } from 'antd';

import SendIcon from '../resources/SendIcon_Blue.png';
import { CommentItemPhoto } from "./CommentItem";
import { colors } from "../../../../styles/variables";

/* Styled Components */

/* CommentInput     div 
   (CommentItemPhoto + CommentInputTextArea)
   isSubComment(bool) props  Sub   3rem  . 
   isDisplay(bool)   false  display none . */
const CommentInputArea = styled.div`
    margin: 0;

    margin-left: ${props => {
        return props.isSubComment ? '3rem' : 0
    }};

    display: ${props => {
        return props.isDisplay ? 'flex' : 'none'
    }};
    flex-flow: row nowrap;
`;

/* TextArea Send   div . (    div)
         flex item 
   TextArea + SendButton  flex box. */
const CommentInputTextArea = styled.div`
    margin-left: 0.5rem;

    border-radius: 1rem;
    background-color: ${colors.white};
    padding: 0.5rem 1rem 0.25rem 1rem;

    flex: 1;
    display: flex;
    flex-flow: row nowrap;
`;

/* CommentInput TextArea    flex item */
const TextAreaDiv = styled.div`
    flex: 1;
`;

/*     
   'react-textarea-autosize'  
         textarea . */
const TextArea = styled(TextareaAutosize)`
    width: 100%;
    padding: 0;

    /* textarea    */
    resize: none;
    border: none;
    outline: 0;

    line-height: 1.5rem;
    font-size: 0.875rem;
`;

/*      */
const SendButton = styled.button`
    margin-left: 0.5rem;
    height: 1.5rem;
    width: 1rem;

    /* button    */
    padding: 0;
    outline: none;
    border: none;
    cursor: pointer;
    
    background-image: ${props => `url(${props.ImgURL})`};
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
`;


/* React Component */

class CommentInput extends Component {

    // state text  
    state = { content: '' }

    
    /*      textarea   */
    componentDidMount () {
        const { isUpdateMode } = this.props;

        if (isUpdateMode){
            this.textRef.focus();
        }
    }

    /*  ,  state   */
    _handleChange = (e) => {
        this.setState({
            content: e.target.value
        })

        // console.log(this.state.content);
    }

    /*   POST   DB  ,
       Post Detail   GET   . or  */
    _handleClick = () => {
        const {
            isUpdateMode,
            updateComment,
            comment_id,

            curUser,

            board,
            post_id,
            createComment,

            parent_comment,     // subComment  
        } = this.props;

        const { content } = this.state;

        /*    ,      */
        if(!curUser) {
            message.warn(' ')
            return
        }

        /*      */
        if(!content) { 
            message.warning('  ')
            return
        }

        /* Update Comment Action */
        if (isUpdateMode) {
            updateComment(comment_id, content);
        }

        /* Create Comment Action */
        else {
            if (parent_comment) {
                createComment(content, board, post_id, parent_comment);
            }
            else {
                createComment(content, board, post_id);
            }
        }
        

        /*    .
            socket.io     */
        window.location.reload();
    }

    /*     (Enter, Esc) */
    /* KeyDown  + Enter ,      */
    _handleKeyPress = (e) => {
        switch(e.key) {
            case 'Enter':
                if(e.shiftKey) { console.log('shift + enter'); break; }

                e.preventDefault();
                this._handleClick();
                break;

            default:
                return;        
        }
    }

    /* KeyPress Esc    */
    _handleKeyDown = (e) => {

        const { 
            isUpdateMode,
            cancleUpdate
        } = this.props;

        switch(e.key) {
            case 'Escape':
                if ( !isUpdateMode ) { return }
                cancleUpdate();
                break;

            default:
                return;
        }
    }


    render() {

        const { 
            isSubComment,
            isReplySee,

            defaultValue
        } = this.props;

        return (
            <CommentInputArea 
                isSubComment={isSubComment} 
                isDisplay={isReplySee}
            >
                <CommentItemPhoto />
                <CommentInputTextArea>
                    <TextAreaDiv>
                        <TextArea 
                            inputRef={tag => {this.textRef = tag}}

                            onKeyDown={this._handleKeyDown} 
                            onKeyPress={this._handleKeyPress}
                            onChange={this._handleChange} 
                            defaultValue={defaultValue} 
                        />
                    </TextAreaDiv>
                    <SendButton 
                        ImgURL={SendIcon} 
                        onClick={this._handleClick}
                    />                   
                </CommentInputTextArea>
            </CommentInputArea>
        )
    }
}


CommentInput.propTypes = {
    isSubComment: PropTypes.bool.isRequired,        // SubComment.  margin-left  .
    isReplySee: PropTypes.bool,                     // SubComment      CommentInput display none .
    isUpdateMode: PropTypes.bool,                   //   Input 

    /*     props */
    board: PropTypes.string.isRequired,
    post_id: PropTypes.number.isRequired,
    parent_comment: PropTypes.number,

    createComment: PropTypes.func,                  //   
    updateComment: PropTypes.func,                  //   
    comment_id: PropTypes.number,                   //   ,   id
    defaultValue: PropTypes.string,                 //   ,    

    curUser: PropTypes.oneOfType([                  //  
        PropTypes.number,
        PropTypes.object
    ]).isRequired,

    cancleUpdate: PropTypes.func,                   //    
}

CommentInput.defaultProps = {
    isReplySee: true,                           // SubComment   isReplySee   display  true .
    isUpdateMode: false,
    parent_comment: 0,
    comment_id: 0,
    defaultValue: '',

    createComment: () => {},
    updateComment: () => {},
    cancleUpdate: () => {},
}


export default CommentInput;