/*  : CommentItem.js */

import React, { Component } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { DeleteOutlined, EditOutlined, MoreOutlined } from '@ant-design/icons';
import { Popover, Tooltip, Modal } from 'antd';

const { confirm } = Modal;

/* Styled Components */
/*          */
const MoreIcon = styled(MoreOutlined)`
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: ${ props => {
        return props.visible ? 'visible' : 'hidden'
    }};
`

/* Popover Content Text Style */
const PopoverContentText = styled.p`
    margin: 0.25rem 0;
    word-spacing: 0.5rem;
    
    cursor: pointer;
`;


/* React Component */
class MoreButton extends Component {

    state = { 
        popoverVisible: false
    }

    /*      (Modal confirm) */
    _showDeleteConfirm = (content) => {
        this.setState({
            ...this.state,
            popoverVisible: false
        })
        
        confirm({
            title: '',
            content,
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk: () => {this._handleDelete()},
            onCancel() {},
            maskClosable: true,
        });
    }

    /* Popover visible   */
    _handlePopVisibleChange = (visible) => {
        this.setState({
            ...this.state,
            popoverVisible: visible
        })
    }

    /*     */
    _handleDelete = () => {

        const { 
            comment_id, 
            deleteComment, 
            subCommentList,
        } = this.props;

        /*   ,    */
        if ( subCommentList[0] ) {
            subCommentList.forEach( subComment => {
                deleteComment(subComment.id)
            })
        }

        /*    */
        deleteComment(comment_id);        

        /*   
            socket.io      */
        window.location.reload();
    }

    
    render () {

        const {
            subCommentList,
            isVisible,

            setUpdateMode
        } = this.props;

        const {
            popoverVisible
        } = this.state;

        const deleteMessage = subCommentList[0] ?
        '     .' :
        '  ?' ;

        /* Popover Content */
        const PopoverContent = (
            <div>
                <PopoverContentText
                    onClick={setUpdateMode}
                >
                    <EditOutlined /> 
                </PopoverContentText>                
                <PopoverContentText 
                    onClick={() => this._showDeleteConfirm(deleteMessage)} 
                >
                    <DeleteOutlined /> 
                </PopoverContentText>
            </div>
        )

        // console.log(typeof(visible))
        // console.log(isVisible.toString())

        return (
            <Tooltip title='  ' mouseEnterDelay={0} mouseLeaveDelay={0}>
                <Popover
                    trigger="click"
                    content={PopoverContent}
                    placement='bottom'
                    visible={popoverVisible}
                    onVisibleChange={this._handlePopVisibleChange}
                >                                    
                    <MoreIcon 
                        rotate='90' 
                        visible={isVisible} 
                    />
                </Popover>
            </Tooltip>
        )
    }
}

MoreButton.propTypes = {
    comment_id: PropTypes.number.isRequired,    //  ID
    subCommentList: PropTypes.array,            //   array
    deleteComment: PropTypes.func.isRequired,   //   
    isVisible: PropTypes.bool,                  //   

    setUpdateMode: PropTypes.func.isRequired,   //    
}

MoreButton.defaultProps = {
    subCommentList: [],
    isVisible: true,
}


export default MoreButton;